<template>
    <section>
        <div class="navbar-container box-shadow">
            <div class="container-fluid">
                <div class="navbar-header flex pull-left">
                    <span class="page-title">报警联系组</span>
                </div>
                <div class="navbar-tool flex pull-right">
                    <el-button class="cds-button" type="primary" @click="add_group('add')">新建联系组</el-button>
                </div>
            </div>
        </div>
        <div class="content">
            <div id="box" v-if="contact_group_list.length > 0" v-for="(m, i) in contact_group_list" :key="i">
                <div class="group_hearder">
                    <div class="header_icon"></div>
                    <div class="header_title">
                        <el-tooltip :content="m.group_description" effect="light" placement="top" v-if="m.group_description">
                            <span v-text="m.group_name"></span>
                        </el-tooltip>
                        <span v-else v-text="m.group_name"></span>
                    </div>
                    <div class="header_action">
                        <ul class="flex">
                            <li>
                                <span class="dark pointer" @click="update_group(m)"><i class="el-icon-edit"></i></span>
                            </li>
                            <li><span>|</span></li>
                            <li>
                                <span class="dark pointer" @click="delete_group('delete_group', m)"><i class="el-icon-close"></i></span>
                            </li>
                            <li><span>|</span></li>
                            <li style="margin-right: 20px">
                                <span class="dark pointer" v-if="m.collapse"  @click="open_group(i)" onselectstart="return false"><i class="el-icon-arrow-down"></i></span>
                                <span class="dark pointer" v-else @click="close_group(i)" onselectstart="return false"><i class="el-icon-arrow-up"></i></span>
                            </li>
                        </ul>
                    </div>
                </div>
                <el-table v-if="!m.collapse" class="table--transparent" :data="m.group_contact">
                    <el-table-column label="姓名" prop="contact_name"></el-table-column>
                    <el-table-column label="邮箱" prop="contact_email"></el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <ul class="flex flex-center">
                                <li>
                                    <span class="light pointer" @click="add_contact_person('update', scope.row)">编辑</span>
                                </li>
                                <li>
                                    <span v-if="m.group_contact.length > 1" class="light pointer" @click="delete_person(m, scope.row, 'delete_group_person')">删除</span>
                                    <span v-else class="light pointer" @click="delete_person(m, scope.row, 'delete_one_person')">删除</span>
                                </li>
                            </ul>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="pull-right" style="width: 100%" v-if="contact_group_list.length > 0">
                <el-pagination class="pull-right" :background="true" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                               :current-page="pageInfo.currentPage" :page-size="pageInfo.pageSize"
                               prev-text="上一页" next-text="下一页"
                               layout="prev, pager, next, jumper" :total="pageInfo.total"></el-pagination>
            </div>
            <div class="card border background-fff" v-if="contact_group_list.length == 0" style="height: 64px;">
                <span>暂无数据!</span>
            </div>
            <msg-dialog :param="msgParam" :data="operateData" @dialogFn="onDialog"  @close="msgParam.visible = false"></msg-dialog>
            <add-contact-person :param="addParams" @personSubmitFn="get_group_list" @close="addParams.visible = false"></add-contact-person>
            <add-contact-group :param="addGroupParams" @submitFn="add_contact_person" @close="addGroupParams.visible = false"></add-contact-group>
        </div>
    </section>
</template>

<script>
import httpServer from "../../service/alarm/alarm_contact_group";
import NcTransfer from "@/components/nc-transfer";
import { Message } from "element-ui";
import addContactPerson from "../../components/alarm/add_contact_person";
import addContactGroup from "../../components/alarm/add_contact_group";
import msgDialog from "@/components/alarm/msgDialog";
export default {
  name: "alarm_contact_group",
  components: { NcTransfer, addContactPerson, addContactGroup, msgDialog },
  data() {
    return {
      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total: 0
      },
      contact_group_list: [],
      addParams: {
        title: "",
        form: { contact_name: "", contact_email: "", code: "" },
        visible: false
      },
      addGroupParams: {
        type: "",
        visible: false,
        updateData: "",
        groupList: []
      },
      msgParam: {
        visible: false
      },
      operateData: null,
      operateList: {
        delete_person: {
          title: "删除联系人",
          msg: "确定删除选择的联系人！",
          msg2: "删除后将不能恢复。"
        },
        delete_group: {
          title: "删除联系组",
          msg: "确定删除选择的联系组！",
          msg2: "删除后将不能恢复。"
        },
        delete_group_person: {
          title: "删除组内联系人",
          msg: "确定删除选择的组内联系人！",
          msg2: "删除后将不能恢复。"
        },
        delete_one_person: {
          title: "删除组内联系人",
          msg: "当前联系组只剩下一个联系人，不能删除！",
        }
      },
      currentGrouplist: [],
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageInfo.pageSize = val;
      this.pageInfo.currentPage = 1;
      this.get_group_list();
    },
    handleCurrentChange(val) {
      this.pageInfo.currentPage = val;
      this.get_group_list();
    },
    resetForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
      });
    },
    // 查看报警联系组及所包含联系人
    get_group_list() {
      let reqData = {
        group_name: "",
        page_size: this.pageInfo.pageSize,
        page_number: this.pageInfo.currentPage
      };
      httpServer
        .get_contact_group_info_list(reqData)
        .then(response => {
          let data = response.data;
          if (data && response.code == "Success") {
            this.contact_group_list = data;
            this.pageInfo.total = response.total_count;
            this.contact_group_list.forEach(item => {
              this.$set(item, 'collapse', true)
            });
            if (this.$route.params.id) {
              this.contact_group_list.forEach(item => {
                if (item.group_id == this.$route.params.id) {
                  this.$set(item, 'collapse', false);
                }
              })
            } else {
              this.$set(this.contact_group_list[0], 'collapse', false);
            }
            this.contact_group_list.forEach(item => {
                this.currentGrouplist.forEach(m => {
                    if (item.group_id == m.group_id) {
                        this.$set(item, 'collapse', m.collapse)
                    }
                })
            });
            console.log('............', this.contact_group_list)
          } else {
            Message({ message: response.msg, type: "error" });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 新建联系组 -弹框
    add_group() {
      this.addGroupParams.type = "add";
      this.addGroupParams.visible = true;
      this.addGroupParams.groupList = this.contact_group_list;
      this.addGroupParams.title = '新建组'
    },
    // 修改联系组 -弹框
    update_group(val) {
      this.addGroupParams.type = "update";
      this.addGroupParams.visible = true;
      this.addGroupParams.updateData = val;
        this.addGroupParams.title = '编辑组'
    },
    // 删除联系组 -弹框
    delete_group(type, val) {
      this.operateData = this.operateList[type];
      this.operateData.type = type;
      this.operateData.group_ids = val.group_id;
      this.msgParam.visible = true;
    },
    onDialog () {
      this.get_group_list()
    },
    open_group(i) {
      this.$set(this.contact_group_list[i], 'collapse', false);
      this.currentGrouplist = this.contact_group_list
    },
    close_group(i) {
      this.$set(this.contact_group_list[i], 'collapse', true);
      this.currentGrouplist = this.contact_group_list
    },
    add_contact_person(type, val) {
      this.addParams.visible = true;
      this.addParams.type = type;
      if (type == "add") {
        // this.$router.push('/alarm_contact_person');
        this.addParams.form = { contact_name: "", contact_email: "", code: "" };
        this.addParams.title = "新建联系人";
        this.addParams.visible = true;
        this.addParams.personList = val;
        this.addGroupParams.visible = false;
        this.addParams.goPerson = true;
      } else if (type == "update") {
        this.addParams.title = "编辑联系人";
        this.addParams.visible = true;
        this.addParams.form = Object.assign({}, val);
      } else if (type == "group") {
        this.addParams.visible = false;
        this.get_group_list();
      }
    },
    // 删除组内联系人 -弹框
    delete_person(m, val, type) {
      this.operateData = this.operateList[type];
      this.operateData.type = type;
      this.operateData.group_id = m.group_id;
      this.operateData.contact_ids = val.contact_id;
      this.msgParam.visible = true;
    },
  },
  mounted() {
    this.get_group_list();
  }
};
</script>

<style scoped>
#box:nth-of-type(n + 2) {
  margin-top: 20px;
}
.dark {
  color: #333;
}
</style>
