<template>
    <section>
        <el-dialog :title="param.title" :visible.sync="param.visible " class="cds-dialog" :before-close="closeDialog">
            <div label-width="120">
                <div class="dialog-body">
                    <el-form :model="groupForm" ref="groupForm" :rules="updateGroupFormRules" label-position="right" label-width="100px">
                        <el-form-item label="组名:" prop="group_name">
                            <el-input v-if="param.type == 'update'" v-model="groupForm.group_name" disabled style="width: 300px;"></el-input>
                            <el-input v-else v-model="groupForm.group_name" style="width: 300px;"></el-input>
                        </el-form-item>
                        <el-form-item label="备注:" prop="description">
                            <el-input type="textarea" :row="2" maxlength="256" show-word-limit v-model="groupForm.group_description" style="width: 300px"></el-input>
                        </el-form-item>
                        <el-form-item label="选择联系人:">
                            <el-transfer
                                    style="text-align: left; display: inline-block"
                                    v-model="selected_person"
                                    filterable
                                    :titles="['已有联系人', '已选联系人']"
                                    :button-texts="['', '']"
                                    :format="{
                                                noChecked: '${total}',
                                                hasChecked: '${checked}/${total}'
                                            }"
                                    @change="handleChange"
                                    :data="noSelected_person">
                                <el-button class="transfer-footer btn--full" type="primary" slot="left-footer" size="small" @click="add_contact_person('add')">新建联系人</el-button>
                            </el-transfer>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button class="cds-button" type="primary" v-on:click="update_group_submit(true, param.type)">确定</el-button>
                <el-button class="cds-button" v-on:click="closeDialog">取消</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import { Message } from "element-ui";
import httpServer from "../../service/alarm/alarm_contact_group";
export default {
  name: "add_contact_group",
  props: {
    param: {
      type: Object,
      default: {}
    }
  },
  data() {
    var checkName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入组姓"));
      } else {
        let result = this.checkName(value);
        if (!result) {
          callback(new Error('组名应由1-40位中文、英文、数字、下划线、"."、"@"、"-"组成'));
        } else {
            if (this.param.type == 'add') {
                this.same_groupName_status = false;
                this.param.groupList.forEach(item => {
                    if (item.group_name == this.groupForm.group_name) {
                        this.same_groupName_status = true
                    }
                });
                if (this.same_groupName_status) {
                    callback(new Error('组名已存在'))
                } else {
                    this.same_groupName_status = false;
                    callback()
                }
            } else {
                callback()
            }
        }
      }
    };
    return {
      groupForm: {
        group_name: "",
        group_description: ""
      },
      all_person: [],
      selected_person: [],
      noSelected_person: [],
      updateGroupFormRules: {
        group_name: [
          { required: true, message: "请输入组名", trigger: "blur" },
          { validator: checkName, trigger: "blur" }
        ]
      },
      same_groupName_status: false
    };
  },
  watch: {
    param: {
      deep: true,
      handler(val) {
        if (val.visible == true) this.get_contact_list(val);
      }
    },
    selected_person: {
      handler(newVal, oldVal) {
        if (newVal === oldVal) {
          return
        } else {
          if (newVal.length > 10) {
            this.selected_person = oldVal;
            Message({message: '一个联系组最多可添加10个联系人', type: 'error'})
          }
        }
      }
    }
  },
  methods: {
    handleChange(val, dir, arr) {
      // console.log("000000000", val); // 当前key数组
      // console.log("111111111", dir); // 移动方向
      // console.log("222222222", arr); // 发生移动的key数组
      // console.log("---------", this.selected_person);
    },
    FnSetEmpty() {
      this.param.visible = false;
      this.param.updateData = "";
      this.param.type = "";
      this.groupForm.group_name = "";
      this.groupForm.group_description = "";
      this.selected_person = [];
      this.noSelected_person = [];
    },
    closeDialog() {
      this.FnSetEmpty();
      this.$emit("close");
    },
    // 查看报警联系人
    get_contact_list(val) {
      let reqData = {

      };
      this.groupForm.group_name = "";
      this.groupForm.group_description = "";
      this.selected_person = [];
      this.noSelected_person = [];
      httpServer
        .get_contact_list(reqData)
        .then(response => {
          let data = response.data;
          if (data && response.code == "Success") {
            this.all_person = data;
              if (val.type == "add") {
                  this.groupForm.group_name = "";
                  this.groupForm.description = "";
                  this.noSelected_person = [];
                  if (this.all_person.length > 0) {
                      this.all_person.forEach(item => {
                          this.noSelected_person.push({
                              key: item.contact_id,
                              label: item.contact_name
                          });
                      });
                  }
              } else if (val.type == "update") {
                  this.selected_person = [];
                  this.noSelected_person = [];
                  this.groupForm = Object.assign({}, this.param.updateData);
                  let currentList = this.param.updateData.group_contact;
                  currentList.forEach(item => {
                      this.selected_person.push(item.contact_id);
                  });
                  this.all_person.forEach(item => {
                      this.noSelected_person.push({
                          key: item.contact_id,
                          label: item.contact_name
                      });
                  });
              }
          } else {
            Message({ message: response.msg, type: "error" });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 新建组 修改组 --提交
    update_group_submit(type, val) {
      if (type == true) {
        this.$refs.groupForm.validate(valid => {
          if (valid) {
            if (this.selected_person.length > 0) {
              let reqData = {
                group_name: this.groupForm.group_name,
                group_description: this.groupForm.group_description,
                contact_ids: this.selected_person
              };
              if (val == "add") {
                if (!this.same_groupName_status) {
                  httpServer
                    .add_contact_group(reqData)
                    .then(response => {
                      let data = response.data;
                      if (data && response.code == "Success") {
                        Message({ message: response.msg, type: "success" });
                        this.FnSetEmpty();
                        this.$emit("submitFn", "group");
                      } else {
                        Message({ message: response.msg, type: "error" });
                      }
                    })
                    .catch(err => {
                      console.log(err);
                    });
                }
              } else {
                reqData.group_id = this.param.updateData.group_id;
                httpServer
                  .update_contact_group(reqData)
                  .then(response => {
                    let data = response.data;
                    if (data && response.code == "Success") {
                      Message({ message: response.msg, type: "success" });
                      this.FnSetEmpty();
                      this.$emit("submitFn", "group");
                    } else {
                      Message({ message: response.msg, type: "error" });
                    }
                  })
                  .catch(err => {
                    console.log(err);
                  });
              }
            } else {
              Message({ message: "请选择联系人", type: "error" });
            }
          }
        });
      } else {
        this.FnSetEmpty();
      }
    },
    add_contact_person(type, val) {
      this.$emit("submitFn", type, this.all_person);
    },
    checkName(name) {
      let regNumber = /\d+/;
      let regCapital = /[A-Z]+/;
      let regLower = /[a-z]+/;
      let regSpecial = /[._@-]+/;
      let regChinese = /[\u4E00-\u9FA5]+/;
      let nameSecurityFlag = 0;
      let flag = true;
      if (regNumber.test(name)) {
        // 数字
        nameSecurityFlag++;
      }
      if (regCapital.test(name)) {
        // 大写字母
        nameSecurityFlag++;
      }
      if (regLower.test(name)) {
        // 小写字母
        nameSecurityFlag++;
      }
      if (regSpecial.test(name)) {
        // 特殊符号
        nameSecurityFlag++;
      }
      if (regChinese.test(name)) {
        // 中文
        nameSecurityFlag++;
      }
      if (name.length < 1 || name.length > 40 || nameSecurityFlag < 1) {
        flag = false;
      }
      return flag;
    }
  },
  mounted() {

  }
};
</script>

