<template>
  <div class="el-transfer nc-transfer" :class="[!!type && 'nc-transfer--'+type]">
    <transfer-panel
      v-bind="$props"
      :data="sourceData"
      :title="titles[0] || t('el.transfer.titles.0')"
      :default-checked="leftDefaultChecked"
      :placeholder="filterPlaceholder || t('el.transfer.filterPlaceholder')"
      @nc-tag--click="onSourceClick"
      @nc-tag--close="onSourceClose"
      @checked-change="onSourceCheckedChange">
      <slot name="left-footer"></slot>
    </transfer-panel>
    <div class="el-transfer__buttons" v-if="type !=='tag'">
      <el-button
        type="primary"
        :class="['el-transfer__button', hasButtonTexts ? 'is-with-texts' : '']"
        @click.native="addToLeft"
        :disabled="rightChecked.length === 0">
        <i class="el-icon-arrow-left"></i>
        <span v-if="buttonTexts[0] !== undefined">{{ buttonTexts[0] }}</span>
      </el-button>
      <el-button
        type="primary"
        :class="['el-transfer__button', hasButtonTexts ? 'is-with-texts' : '']"
        @click.native="addToRight"
        :disabled="leftChecked.length === 0">
        <span v-if="buttonTexts[1] !== undefined">{{ buttonTexts[1] }}</span>
        <i class="el-icon-arrow-right"></i>
      </el-button>
    </div>
    <div class="el-transfer__buttons" v-if="type ==='tag'">
      <el-button
        type="primary"
        :class="['el-transfer__button', hasButtonTexts ? 'is-with-texts' : '']"
        @click.native="addToLeftTag"
        :disabled="targetData.length === 0">
        <i class="el-icon-arrow-left"></i>
        <span v-if="buttonTexts[0] !== undefined">{{ buttonTexts[0] }}</span>
      </el-button>
      <el-button
        type="primary"
        :class="['el-transfer__button', hasButtonTexts ? 'is-with-texts' : '']"
        @click.native="addToRightTag"
        :disabled="sourceData.length === 0">
        <span v-if="buttonTexts[1] !== undefined">{{ buttonTexts[1] }}</span>
        <i class="el-icon-arrow-right"></i>
      </el-button>
    </div>
    <transfer-panel
      class="no-close"
      v-bind="$props"
      :data="targetData"
      :title="titles[1] || t('el.transfer.titles.1')"
      :default-checked="rightDefaultChecked"
      :placeholder="filterPlaceholder || t('el.transfer.filterPlaceholder')"
      @nc-tag--click="onTargetClick"
      @nc-tag--close="onTargetClose"
      @checked-change="onTargetCheckedChange">
      <slot name="right-footer"></slot>
    </transfer-panel>
  </div>
</template>
<style>
.nc-transfer-flex {display:flex;align-items:center;}
.nc-transfer-flex .el-transfer-panel {flex:1;}
.nc-transfer .el-transfer-panel {background-color:transparent;border:none;}
.nc-transfer .el-transfer-panel__body {background-color:#FFF;border:1px solid #DDE2EF;}
.nc-transfer .el-transfer-panel__header .el-checkbox .el-checkbox__label {color:#576077;}
.nc-transfer--tag .el-transfer-panel .el-transfer-panel__header {background-color:transparent;}
.nc-transfer--tag .el-transfer-panel__header .el-checkbox__input {display:none;}
.nc-transfer--tag .el-transfer-panel__header .el-checkbox {cursor:default;}
.nc-transfer--tag .el-transfer-panel__header .el-checkbox .el-checkbox__label {font-size:12px;color:#7F86A5;padding:0;}
.nc-transfer--tag .el-transfer-panel__header .el-checkbox .el-checkbox__label > * {display:none;}
.nc-transfer--tag .el-transfer-panel .el-transfer-panel__footer {background-color:#F7F9FC;border-top:1px solid #DDE2EF;}
.nc-transfer--tag .el-transfer-panel__footer .el-button--default {padding:0 20px;}
.nc-transfer--tag .el-transfer-panel__footer .search-group {width:100%;background-color:transparent;border:none;}
.nc-transfer--tag .el-transfer-panel__empty {display:none;}
.nc-transfer--no-footer .el-transfer-panel__body {padding-bottom:0;}
.nc-transfer--no-footer .el-transfer-panel__footer {display:none;}
</style>
<script>
/* eslint-disable*/
  import Emitter from 'element-ui/src/mixins/emitter';
  import Locale from 'element-ui/src/mixins/locale';
  import Migrating from 'element-ui/src/mixins/migrating';
  import TransferPanel from './transfer-panel.vue';
  // import Migrating from 'element-ui/src/mixins/migrating';

  export default {
    name: 'NcTransfer',
    componentName: 'NcTransfer',
    mixins: [Emitter, Locale, Migrating],

    components: {
      TransferPanel
    },

    props: {
      type: {
        type: String,
        default: ''
      },
      istips: {
        type: Boolean,
        default: false
      },
      isremove: {
        type: Boolean,
        default: false
      },
      tagWidth: {
        type: Number,
        default: 150
      },
      data: {
        type: Array,
        default() {
          return [];
        }
      },
      titles: {
        type: Array,
        default() {
          return [];
        }
      },
      buttonTexts: {
        type: Array,
        default() {
          return [];
        }
      },
      filterPlaceholder: {
        type: String,
        default: ''
      },
      filterMethod: Function,
      leftDefaultChecked: {
        type: Array,
        default() {
          return [];
        }
      },
      rightDefaultChecked: {
        type: Array,
        default() {
          return [];
        }
      },
      renderContent: Function,
      value: {
        type: Array,
        default() {
          return [];
        }
      },
      format: {
        type: Object,
        default() {
          return {};
        }
      },
      filterable: Boolean,
      props: {
        type: Object,
        default() {
          return {
            label: 'label',
            key: 'key',
            status: 'status',
            disabled: 'disabled'
          };
        }
      }
    },

    data() {
      return {
        leftChecked: [],
        rightChecked: []
      };
    },

    computed: {
      sourceData() {
        // let _a = []
        // this.data.forEach(item => {
        //   let _b = this.value.find(m => m[this.props.key] === item[this.props.key] && !m[this.props.status])
        //   if (!_b) {
        //     _a.push(Object.assign(item,{status: 0}))
        //     console.log(2222222, _a)
        //   }
        // })
        // return _a
        return this.data.filter(item =>
          !this.value.find(m => m[this.props.key] === item[this.props.key] && !m[this.props.status])
        );
        // return this.data.filter(item => this.value.indexOf(item[this.props.key]) === -1);
      },

      targetData() {
        let _a = []
        this.data.forEach(item => {
          let _b = this.value.find(m => m[this.props.key] === item[this.props.key])
          if (!!_b) {
            _a.push(Object.assign({},item,_b))
          }
        })
        return _a
        // return this.data.filter(item => this.value.indexOf(item[this.props.key]) > -1);
      },

      hasButtonTexts() {
        return this.buttonTexts.length === 2;
      }
    },

    watch: {
      value(val) {
        this.dispatch('ElFormItem', 'el.form.change', val);
      }
    },

    methods: {
      getMigratingConfig() {
        return {
          props: {
            'footer-format': 'footer-format is renamed to format.'
          }
        };
      },

      onSourceCheckedChange(val) {
        this.leftChecked = val;
      },

      onTargetCheckedChange(val) {
        this.rightChecked = val;
      },

      onSourceClick(item) {
        for(let i in this.value) {
          if (this.value[i][this.props.key] === item[this.props.key])
            return this.value[i][this.props.status] = null
        }
        this.value.push(item)
      },

      onTargetClick(item) {
        for(let i = 0; i < this.value.length; i++) {
          if (this.value[i][this.props.key] === item[this.props.key])
            return this.value.splice(i,1)
        }
      },

      onSourceClose(item) {
        this.$emit('nc-tag--close', item)
        // this.data.splice(this.data.indexOf(item), 1)
      },

      onTargetClose(item) {
        this.onTargetClick(item)
        this.$emit('nc-tag--close', item)
        // this.data.splice(this.data.indexOf(item), 1)
      },

      addToLeftTag () {
        this.value.splice(0)
      },

      addToRightTag () {
        this.value.splice(0)
        for(let i = 0; i < this.data.length; i++) {
          this.value.push(this.data[i])
        }
      },

      addToLeft() {
        let currentValue = this.value.slice();
        this.rightChecked.forEach(item => {
          const index = currentValue.indexOf(item);
          if (index > -1) {
            currentValue.splice(index, 1);
          }
        });
        this.$emit('input', currentValue);
        this.$emit('change', currentValue, 'left', this.rightChecked);
      },

      addToRight() {
        let currentValue = this.value.slice();
        this.leftChecked.forEach(item => {
          if (this.value.indexOf(item) === -1) {
            currentValue = currentValue.concat(item);
          }
        });
        this.$emit('input', currentValue);
        this.$emit('change', currentValue, 'right', this.leftChecked);
      }
    }
  };
</script>
